// @flow
import invariant from 'invariant';

import App from 'lib/App';

const root = document.getElementById('root');
invariant(root, 'Root element must exist');

function colorPixel(
  canvasImage: ImageData,
  x: number,
  y: number,
  red: number,
  green: number,
  blue: number,
): void {
  const pixelIdx = (y * canvasImage.width + x) * 4;
  canvasImage.data[pixelIdx] = red;
  canvasImage.data[pixelIdx + 1] = green;
  canvasImage.data[pixelIdx + 2] = blue;
  canvasImage.data[pixelIdx + 3] = 255;
}

function createCanvas(width: string, height: number): HTMLCanvasElement {
  const canvas = document.createElement('canvas');
  canvas.setAttribute('width', String(width));
  canvas.setAttribute('height', String(height));

  const context = canvas.getContext('2d');
  const imageData = context.createImageData(width, height);
  for (let x = 0; x < width; x++) {
    for (let y = 0; y < height; y++) {
      colorPixel(imageData, x, y, 100, 100, 100);
    }
  }
  context.putImageData(imageData, 0, 0);
  return canvas;
}

root.appendChild(createCanvas(400, 200));

export default App;
